"use client";
import { supabase } from "@/lib/supabase";
import { useEffect, useState } from "react";

export default function EntryGate() {
  const [loading, setLoading] = useState(true);
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [mode, setMode] = useState<"guest" | "admin">("guest");

  useEffect(() => {
    const checkAuth = async () => {
      const { data: { user } } = await supabase.auth.getUser();
      if (user) routeUser(user.id);
      else setLoading(false);
    };
    checkAuth();
  }, []);

  const routeUser = async (userId: string) => {
  const { data: profile } = await supabase
    .from("profiles")
    .select("is_admin, is_owner")
    .eq("id", userId)
    .single();
  console.log("PROFILE CHECK:", profile);
  console.log("ROUTING PROFILE:", profile);

  if (profile?.is_admin) {
    window.location.href = "/dashboard/admin";
  } else if (profile?.is_owner) {
    window.location.href = "/dashboard/owner";
  } else {
    window.location.href = "/dashboard/guest";
  }
};



  const loginEmail = async () => {
  if (!email) {
    alert("Enter email");
    return;
  }

  const { error } = await supabase.auth.signInWithOtp({
  email,
  options: {
    emailRedirectTo: `${location.origin}/`,
  },
});



  if (error) {
    alert(error.message);
  } else {
    alert("Magic link sent to email");
  }
};


  const signupEmail = async () => {
    const { error } = await supabase.auth.signUp({
      email,
      password,
    });
    if (!error) alert("Account created. Please login.");
  };

  const loginGoogle = async () => {
   await supabase.auth.signInWithOAuth({
  provider: "google",
  options: {
    redirectTo: `${location.origin}/`,
  },
});
  };

  if (loading) return <p className="p-10">Loading...</p>;

  return (
    <main className="min-h-screen flex items-center justify-center">
      <div className="w-96 border rounded-xl p-8 space-y-4">

        <h1 className="text-2xl font-bold text-center">
          {mode === "guest" ? "Book stays in Alibag" : "Hotel Owner Login"}
        </h1>

        <button
          onClick={loginGoogle}
          className="w-full border p-2 rounded"
        >
          Continue with Google
        </button>

        <hr />

        <input
          className="border p-2 w-full"
          placeholder="Email"
          onChange={(e) => setEmail(e.target.value)}
        />
        <input
          className="border p-2 w-full"
          type="password"
          placeholder="Password"
          onChange={(e) => setPassword(e.target.value)}
        />

        <button
          className="bg-black text-white w-full p-2"
          onClick={loginEmail}
        >
          Login
        </button>

        {mode === "guest" && (
          <button
            className="text-sm underline w-full"
            onClick={signupEmail}
          >
            Create customer account
          </button>
        )}

        <div className="pt-4 text-center">
          {mode === "guest" ? (
            <button
              className="text-sm underline"
              onClick={() => setMode("admin")}
            >
              Are you a hotel owner?
            </button>
          ) : (
            <button
              className="text-sm underline"
              onClick={() => setMode("guest")}
            >
              Back to customer login
            </button>
          )}
        </div>

      </div>
    </main>
  );
}
